has-text-grey-dark"},[_vm._v("Timeline of the Movements")]),_c('p',[_vm._v("After extracting and assigning the movements to each painter, we had a dataset with movements and the birth years of the painters assigned to those movements. This made it possible to look at the distributions of the birth years of the painters for each movement. By doing so it was possible to obtain an estimation of the movements active years. 30 years were added to the birth years to to getter a better estimate, as we are not interested in the birth year of the, to the movement, assigned painters but in the years where they were performing. The minimum and maximum values of boxplots, represented by the lines should be ignored as they are obscured by outliers, instead one should look at the range from the 1st to the 3rd quartile represented by the box.")])])])])]),_vm._m(13),_c('section',{staticClass:"section"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"columns"},[_c('div',{staticClass:"column is-half is-offset-one-quarter"},[_c('br'),_c('p',[_vm._v("From the above diagram it can be seen that the estimates of the active years of the movements derived from the painters‚Äô birth years are very accurate. We can also see that some important orders are true: Like realism after romanticism, rococo after baroque, post-impressionism after impressionism etc.")]),_c('p',[_vm._v("We can see that centuries ago only one or two movements were present at a time and the movements defined the period, but later many came to exist in parralel.")]),_c('p',[_vm._v("It's interesting to see how the arrival of new movements is quite slow initially until around the 1850s where it explodes and new movements arises every decade. It does seem like there has been a small slow-down again from the 1950s to now. But it might just be due to the fact that some years has to pass before someone picks up the pattern that was the movement and coins it with an \"-ism\". Or because the list is sorted by number of painters assigned to the movements and only the top 35 is shown, the new ones can be thought not have as many members, both because they're new and because so many exists in parallel compared to earlier.")]),_c('br'),_c('h3',{staticClass:"title is-5 has-text-grey-dark"},[_vm._v("Network of Movements using Association Rule Mining")]),_c('p',[_vm._v("Creating a network of painters of course allows one to get an idea of how these painters are connected and who influenced who. But since we have the info on the movements connected to the painters we might also have an opportunity to see how the movements are connected to each other and which movement led to other movements etc.")]),_c('p',[_vm._v("In order to investigate the relationships between the movements I used the already collected movement links for each painter. This time however I didn‚Äôt just use the most frequent movement but all of them. Because that was excactly what I was interested in - how the different movements were mentioned together on the same painter‚Äôs wiki-page. This left me with a set of sets of movements that were mentioned together. Much like the data a supermarket have of items that are bought together when looking at each transaction.")]),_c('p',[_vm._v("To analyse what pairs of items (or pairs of sets of items) are usually bought together and what items imply other items, a supermarket often performs what is known as a market basket analysis AKA association rule mining. Seeing how similar the problems were, I chose to also use association rule mining to find the associations between the different movements and then use those associations as edges in a weighted directed graph of movements. I wanted the directions as I wanted to know which movements had inspired which movements, but they weren‚Äôt obvious from the association rule mining itself. I therefore decided that the best proxy would be to use a chronological causality, meaning that when a relationship between two movements existed, the first movement (by average active time) would always be the antecedent and the latter movement would be the consequence. This made sense since a movement couldn‚Äôt really lead to another movement that was active before that movement. This resulted in the following network of movements:")]),_c('br'),_c('p',{staticStyle:{"font-size":"0.8em","color":"grey"}},[_vm._v("(click the graph for an interactive version)")]),_vm._m(14),_c('br'),_c('br'),_c('br'),_c('p',[_vm._v("If we click the above network and look at the interactive version, we can see that the found connections between the movements seem pretty accurate. From a birds eye perspective, the first thing we notice is that the movements have been positioned with the earliest movements at the top moving gradually toward the most recent movements in the bottom. If we for example take a look at Rococo we see that the network has found it's main influencing movement to be the Baroque, which is true (in the interactive graph, the direction of the edges is given by the curvature. The edge between Baroque and Rococo has the direction Baroque -> Rococo).")]),_c('br'),_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Communities")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('p',[_vm._v("We already have the movements assigned to our painters. The movements can be viewed as communities of painters in their own right. But they‚Äôve been assigned only by looking at the text on the wiki-pages. Therefore, it could be interesting to find communities based on the structure of the network i.e. how the painters are linked to each other. It‚Äôs then interesting to see if these communities follow the divisions of the movements or if they find other logics in the network to base their divisions on. It might be that a group of painters from different movements actually have a stronger connection to each other than to other painters from their own movement. The network has been visualized with the communities added as a colors below. (maybe several of them).")]),_c('br'),_c('br'),_c('a',{attrs:{"href":"https://ketelsen.github.io/CommunityNet/","target":"_blank"}},[_c('img',{attrs:{"src":_vm.selectedComImage}})]),_c('br'),_c('br'),_c('br'),_c('div',{staticClass:"box"},[_c('h1',{staticClass:"title is-5"},[_vm._v("\n              Top Painters and Movements in Community:\n              "),_c('select',{directives:[{name:"model",rawName:"v-model",value:(_vm.selectedCom),expression:"selectedCom"}],on:{"change":function($event){var $$selectedVal = Array.prototype.filter.call($event.target.options,function(o){return o.selected}).map(function(o){var val = "_value" in o ? o._value : o.value;return val}); _vm.selectedCom=$event.target.multiple ? $$selectedVal : $$selectedVal[0]}}},[_c('option',[_vm._v("select")]),_vm._l((_vm.communities),function(community){return _c('option',{key:community},[_vm._v(_vm._s(community))])})],2)]),_c('hr',{staticClass:"Thin"}),_c('div',{staticClass:"columns"},[_c('div',{staticClass:"column is-half"},[_c('h1',{staticClass:"title is-6"},[_vm._v("Top Painters")]),_c('table',{staticClass:"table"},[_vm._m(15),_c('tbody',_vm._l((_vm.selectedTopArtistsCom),function(artist){return _c('tr',[_c('td',[_c('a',{attrs:{"href":artist.link,"target":"_blank"}},[_vm._v(_vm._s(artist.name))])]),_c('td',[_vm._v(_vm._s(artist.lifespan))])])}),0)])]),_c('div',{staticClass:"column is-half"},[_c('h1',{staticClass:"title is-6"},[_vm._v("Top Movements")]),_c('table',{staticClass:"table"},[_vm._m(16),_c('tbody',_vm._l((_vm.selectedTopMovementsCom),function(movement){return _c('tr',[_c('td',[_vm._v(_vm._s(movement.name))]),_c('td',[_vm._v(_vm._s(movement.count))])])}),0)])])])]),_c('br'),_c('p',[_vm._v("From the visualization above we can see that large communities i.e. the first five, seems to capture related groups of movements, primarely related in terms of time. The next five movements capture smaller subcommunities that are specific to a nationality in particular, era and style.")]),_c('p',[_vm._v("Community 3 mainly has impressionists, post-impressionists and realists which are all related movements. So the community partitions seems to capture some of the same logic that govern the movements. Community 9 captures the modernist movements such as the cubists, expressionists and surrealists again all very related movements. Community 14 primarely consists of the baroque and Dutch Golden Age Painters (who were also a part of the baroque movement). Community 0 also has some of the baroque painters in it, but focusing more on the earlier renaissance and manneristic painters. Community 5 consists of the post-modernistic movements. Community 10 consists of the painters after the baroque and before the impressionists. Community 45 are very old Japanese painters. Community 198 are very old chinese painters. The two aforementioned communities are interesting as they are actually almost parallel with the rennaisance painters from community 0, but have not been included with these, since they didn't have much to do with them. They are actually pretty detached from the rest of the communities showing very few connections to those. This shows that although many of the previously mentioned communities are very much defined by time, time is not the cause of the divisions but an effect of the painters of the same era's being influenced by each other. So when painters from the same era are not influenced by each other, like the with the Asian communities they don't share communities. Community 18 and 27 are German and Russion/Eastern European Painters respectively.")]),_c('p',[_vm._v("It's also apparent from the above visualization that the position of the communities shows us something about the relation of the communities and the movements within them. For example we can draw a curved line from community 14 to 10 to 3 to 9 and then to 5, showing the evolution from Baroque to Rococo and Neoclassicism to Realism and Impressionism to Modernism and then to Postmodernism.")]),_c('p',[_vm._v("The same logic is true when we look at the artists within the communities. Often artists that are seen as the bridges between movements can be found in the middle of the movements. This is for example the case for C√©sanne who was seen as a bridge between Impressionism and Cubism (modernist movement) who is placed right at the edge between community 3 and 9.")]),_c('p',[_vm._v("Below the distributions of the birth years of the painters have been visualized:")])])])])]),_vm._m(17),_vm._m(18)])}
