ring the Patterns of Art History through Network Science and Text Analytics\n                  ")]),_c('h2',{staticClass:"subtitle has-text-white is-size-5",staticStyle:{"padding-top":"1em"}},[_vm._v("\n                    In this project I will explore how  art painters and movements throughout history are related to each other by applying methods from network science and statistics to a dataset of painters I have obtained by scraping Wikipedia and applying text analytics to mine the features of the dataset from the texts retrieved. \n                  ")])])])])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"section"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"columns"},[_c('div',{staticClass:"column is-half is-offset-one-quarter"},[_c('br'),_c('p',{staticStyle:{"color":"grey"}},[_vm._v("Disclaimer: This article is a non-technical display of some of the results from the full analysis. For the full analysis and the technical details see the "),_c('a',{attrs:{"href":""}},[_vm._v("Explainer Notebook")]),_vm._v(".")]),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Introduction")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('p',[_vm._v("In this project, we will use the theory and methods of Network Science to investigate the relationships between fine art painters throughout history. Ideally, this analysis will serve several purposes:")]),_c('p',[_vm._v("Firstly it‚Äôs meant as an exploration tool. Being interested in art history is tough! How do we find new painters we might like? Going through Wikipedia‚Äôs endless lists of painters sorted by name takes its toll on even the most enduring of art history enthusiasts. So the point is to make a tool for easier, quicker and more entertaining discovery of artists. Hopefully, the graphical layout of the network will assist with this discovery when compared to other overviews of artists. For example, it‚Äôs likely that artists located nearby each other are somewhat like each other in style, so if one knows and likes a certain artist or movement, then that could be used as a starting point for exploration. Furthermore, the size of the nodes is going to be proportional with the artists‚Äô significance, thereby making it easier not to miss any of the big ones.")]),_c('p',[_vm._v("Secondly, art history was purposefully chosen for its quality of belonging to the humanities. I find it fascinating to apply quantitative methods to qualitative fields of studies such as art history. Since using these quantitative methods is not the usual way of analyzing these fields, it might result in novel discoveries not obvious to the human eye without such tools.")]),_c('p',[_vm._v("Network Science is, in my mind, one of the more useful tools for bridging the gap between the humanities and quantitative analysis. I think its usefulness derives from the fact that on a microscopic level - the level of the individual nodes and links - a network is pretty qualitative, the link between two nodes represents a relationship and that‚Äôs it. But when looked at from a macroscopic perspective - the level of the entire network - all of these qualitative relationships sum up and results in the emergence of a larger system that lends itself to quantitative analysis. It‚Äôs a bit similar to how soap works to bind together fat and water, with one end of the soap molecule being hydrophilic binding to water and the other end hydrophobic binding to fat, it enables the connection of things that otherwise would not connect.")]),_c('p',[_vm._v("So hopefully the use of network science will allow us to discover something we didn‚Äôt know about the system of art history through time.")]),_c('br'),_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Retrieving the Dataset")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('p',[_vm._v("To analyze the painters we first needed to create a dataset that would serve as the basis for the network. Fortunately, Wikipedia has lists of art painters through time sorted by beginning letter of the last name. We used these lists to generate a list of all the painters and then used that list to download every painter‚Äôs own Wikipedia site. This resulted in a dataset with 3223 painters and a combined length of all of their Wikipedia sites of 36,138,404 characters equivalent to 15,058 standard pages (2400 characters/page).")]),_c('br'),_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Constructing the Network")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('p',[_vm._v("Having retrieved the text from each painters Wikipedia site, we then used regular expressions to automatically extract all of the links from each painters site. The links were then filtered so that only the links pointing to other painters Wikipedia sites were left. We then constructed the graph by first creating a node for each painter in our dataset and then by placing an edge from one painter to another if the painter has a link to the other painter‚Äôs Wikipedia site on his site. The idea being that we can use the fact that a painter points to another painter in his wiki-site as a proxy for that painter being influenced by the painter that is pointed to. This way painters with a lot of in-going edges are painters that are mentioned on many other painters‚Äô wiki-sites and can be thought to have been proportionally influential.")]),_c('p',[_vm._v("\n            This resulted in a directed graph with 3223 nodes and 10448 edges. Which means that on average each painter‚Äôs wiki-site points to 3.24 other painters. The resulting graph is visualized below. (click\n            "),_c('u',[_c('a',{attrs:{"href":"https://ketelsen.github.io/CommunityNet/","target":"_blank"}},[_vm._v("the graph")])]),_vm._v(" for an interactive version)\n          ")]),_c('br'),_c('a',{attrs:{"href":"https://ketelsen.github.io/CommunityNet/","target":"_blank"}},[_c('img',{attrs:{"src":require("./assets/main_graph.png")}})]),_c('br'),_c('br'),_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Basic Statistics about the Network")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('h3',{staticClass:"title is-5 has-text-grey-dark"},[_vm._v("Distribution of Birth Year of Painters")]),_c('p',[_vm._v("In order to get to know our dataset a bit better, it could be interesting to look at the distribution of the birth years of our painters. Do we primarely have painters that lived in the distant past or very recently? Let‚Äôs take a look:")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"section has-no-padding shadow"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"columns"},[_c('div',{staticClass:"column is-10 is-offset-1"},[_c('iframe',{attrs:{"id":"plot1","width":"900","height":"800!important","frameborder":"0","scrolling":"no","src":"//plot.ly/~Ketelsen/198.embed?showlink=false"}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"section"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"columns"},[_c('div',{staticClass:"column is-half is-offset-one-quarter"},[_c('p',[_vm._v("Looking at the above histogram we can see that the dataset is primarely comprised of recent painters with a mode at around 1890. Interestingly, we do however see a spike at the beginning of the 17th century. This can probably be attributed to the late renaissance and baroque artist movements, that produced a lot of famous painters.")]),_c('br'),_c('h3',{staticClass:"title is-5 has-text-grey-dark"},[_vm._v("Most Influential Painters")]),_c('p',[_vm._v("Looking at the above histogram we can see that the dataset is primarely comprised of recent painters with a mode at around 1890. Interestingly, we do however see a spike at the beginning of the 17th century. This can probably be attributed to the late renaissance and baroque artist movements, that produced a lot of famous painters.")]),_c('p',[_vm._v("Other algorithms for measuring the centrality of a node (painter) exists as well. The I will show the result of these here as well but not go in to deeper details about how to interpret these other centrality measures. For reference look at the explainer notebook or look up the measures yourself. (eller bare skriv lidt tekst under hver measure).")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"section has-no-padding shadow"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"columns"},[_c('div',{staticClass:"column is-10 is-offset-1"},[_c('iframe',{attrs:{"id":"plot2","width":"900","height":"800!important","frameborder":"0","scrolling":"no","src":"//plot.ly/~Ketelsen/202.embed?showlink=false"}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"section"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"columns"},[_c('div',{staticClass:"column is-half is-offset-one-quarter"},[_c('br'),_c('br'),_c('br'),_c('p',[_vm._v("The result is as expected with all of the most famous painters having the highest in-degrees (and no unknown painters with unexpectedly high in-going degrees). This is great, it verifies that the network works as it should. It can also be seen how the in-degrees follows an exponential (power) distribution which is a property of a scale free network. (?)")]),_c('p',[_vm._v("The above chart is very simple, but in spite of this, pretty interesting. I personally spent quite some time, just exploring the names of the high-ranking painters. It was interesting to find the painters that were very influential, that I had somehow missed and never h